import Vue from 'vue';
import VueRouter from 'vue-router';

import Login from '../views/Login.vue';

import Home from '@/views/Home';

import Layout from '../cabeceras/MenuNavegacionInmueble.vue';
import HomeInmuebles from '../views/Inmueble/InmueblesCliente.vue';
import InmueblesUsuarioCargo from '../views/Inmueble/InmueblesUsuarioCargo.vue'
import CargarArchivo from '../views/Inmueble/CrearInmuebles.vue';

import AsignarTrabajos from '../views/Trabajo/AsignarTrabajos.vue'
import AsignarTrabajosCliente from '../views/Trabajo/AsignarTrabajosCliente.vue'
import AsignarTrabajosUsuario from '../views/Trabajo/AsignarTrabajosUsuario.vue'
import TrabajosUsuario from '@/views/Trabajo/TrabajosUsuario.vue';
import TrabajosInmueble from '../views/Trabajo/TrabajosInmueble.vue'
import TrabajoUsuario from '@/views/Trabajo/ArchivosPorTrabajo.vue';
import TrabajosCliente from '../views/Trabajo/TrabajosCliente.vue'

// import HomeRecurso from '../views/Recurso/Home.vue'
// import RecursosUsuario from '../views/Recurso/SolicitudesRecursosCreadas.vue'
import Recurso from '../views/Recurso/SolicitudesRecursos.vue'
import Pagos from '../views/Pago/Pagos.vue'
import CrearRecurso from '../views/Recurso/CrearRecurso.vue'

import LayoutPersonal from '../cabeceras/MenuNavegacionPersonal.vue';
import HomeUsuarios from '../views/Usuario/Home.vue';
import Privilegios from '../views/Usuario/Privilegios.vue'

import HomeCoordinador from '../cabeceras/MenuNavegacionCoordinador.vue'

import AdministrarPersonal from '../views/Usuario/AdministrarPersonal.vue'

import AdministrarContactos from '../views/Contacto/AdministrarContactos.vue'

import ConfiguracionCliente from '../views/Configuraciones/ClienteInmuebles.vue'

import store from '@/store/index';


import de from '../views/Trabajo/VerTrabajo.vue'

Vue.use(VueRouter);

const enterSiUsuarioAutenticado = (to, from, next) => {
  if (store.getters['MOD_autenticacion/isUsuarioAutenticado']) {
    next();
  } else {
    next('/login')
  }
};

const routes = [
  {
    path: '/login',
    name: 'Login',
    component: Login,
  },
  {
    path: '/:nombreUsuario',
    name: 'HomeUsuario',
    component: HomeUsuarios,
    meta: { requiereAuth: true },
    children: [
      {
        path: 'trabajo/archivos',
        name: 'ArchivosTrabajo',
        component: TrabajoUsuario,
        props: true
      },
      {
        path: 'trabajos/nuevos',
        name: 'asignar-trabajos',
        component: AsignarTrabajos,
        props: true
      },
      {
        path: 'pagos',
        name: 'pagos',
        component: Pagos
      }
    ]
  },
  {
    path: '/coordinador/:nombre_usuario',
    name: 'home-coordinador',
    component: HomeCoordinador,
    meta: { requiereAuth: true },
    children: [
      {
        path: 'inmuebles',
        name: 'inuembles-coordinador',
        component: InmueblesUsuarioCargo
      },
      {
        path: 'trabajos',
        name: 'trabajos-coordinador',
        component: TrabajosUsuario
      },
      {
        path: 'trabajos/nuevos',
        name: 'asignar-trabajos-coordinador',
        component: AsignarTrabajosUsuario,
        props: true
      },
      {
        path: 'inmuebles/:clave_inmueble/trabajos',
        name: 'trabajos-inmueble-coordinador',
        component: TrabajosInmueble,
        props: true
      },
      {
        path: 'peticiones',
        name: 'peticiones-coordinador',
        component: Recurso
      }
    ]
  },
  // {  gestor: prox
  //   path: '/:nombreUsuario/recursos',
  //   component: HomeRecurso,
  //   meta: { requiereAuth: true },
  //   children: [
  //     {
  //       path: 'creados',
  //       name: 'recursos-usuario',
  //       component: RecursosUsuario
  //     },
  //     {
  //       path: 'solicitudes',
  //       name: 'solicitudes',
  //       component: Recurso,
  //     }
  //   ]
  // },
  {
    path: '/peticion/nuevo',
    name: 'peticion-recursos',
    component: CrearRecurso,
  },
  {
    path: '/inmuebles/cargar',
    name: 'cargar-archivo',
    component: CargarArchivo,
    props: true
  },
  {
    path: '/',
    name: 'Home',
    component: Home,
    beforeEnter: enterSiUsuarioAutenticado
  },
  {
    path: '/:nombreUsuario/usuarios',
    component: LayoutPersonal,
    meta: { requiereAuth: true },
    children: [
      {
        path: 'personal',
        name: 'personal',
        component: AdministrarPersonal,
      },
      {
        path: 'privilegios',
        name: 'privilegios',
        component: Privilegios
      },
      {
        path: 'contactos',
        name: 'contactos',
        component: AdministrarContactos
      }
    ]
  },
  {
    path: '/de',
    name: 'De',
    component: de
  },
  {
    path: '/:cliente',
    component: Layout,
    meta: { requiereAuth: true },
    children: [
      {
        path: 'inmuebles',
        name: 'inmuebles',
        component: HomeInmuebles,
      },
      {
        path: 'trabajos',
        name: 'trabajos-cliente',
        component: TrabajosCliente
      },
      {
        path: 'asignar/trabajos/nuevos',
        name: 'asignar-trabajos-cliente',
        component: AsignarTrabajosCliente
      },
      {
        path: 'configuraciones',
        name: 'configuraciones-cliente',
        component: ConfiguracionCliente
      }
    ],
  }
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.requiereAuth)) {
    if (!store.getters['MOD_autenticacion/isUsuarioAutenticado']) {
      next({
        name: 'Login',
      });
    } else {
      next();
    }
  } else {
    next();
  }
});
export default router;
