<template>
    <v-row dense justify="center">
        <v-col xs="12" sm="12" md="8" lg="8" xl="8">
            <v-sheet class="container">
                <tabla-inmuebles
                    :isMostrarMenuInmuebles="false"
                    :ObjFiltrar="{isBtnFiltrar: true, isAutocompleteCliente: true}"
                    texto_sin_datos="No se hallaron propiedades en los estados bajo su responsabilidad."
                    :cabecera="cabecera"
                    :inmuebles="inmuebles"
                    :isCardCabecera="true"
                    :is_mostrar_btn_actualizar="true"
                    @emitFiltro="inicializarInmueblesUsuarioCargo"
                />
            </v-sheet>
        </v-col>
    </v-row>
</template>
<script>
import TablaInmuebles from '../../components/Inmueble/TablaInmuebles.vue'
export default {
    data:()=>({
        cabecera: [
            { value: 'cliente', text: 'Cliente', sortable: false },
            { value: 'clave', text: 'Clave' },
            { text: 'SIRH', value: 'sirh' },
            { value: 'nombre', text: 'Inmueble' },
            { value: 'estado.name', text: 'Estado' },
            { value: 'municipio.name', text: 'Municipio' },
            { value: 'direccion', text: 'Direccion', sortable: false },
            { text: 'Acciones', value: 'acciones', align: 'center' }
        ]
    }),
    components: {
        'tabla-inmuebles': TablaInmuebles,
    },
    computed: {
        inmuebles() { return this.$store.getters['inmueble/getInmuebles'] },
        usuario() {return this.$store.getters['MOD_autenticacion/getUsuarioAutenticado']}
    },
    created(){
        this.inicializarInmueblesUsuarioCargo({cliente: {}, estado: {}, municipio: {}})
    },
    methods:{
        async inicializarInmueblesUsuarioCargo({cliente, estado, municipio}){
            if(Object.keys(this.usuario).length || Object.keys(cliente).length || Object.keys(estado).length || Object.keys(municipio).length){
                const { mensaje, http_estado, error } = await this.$store.dispatch('inmueble/listarInmueblesUsuarioCargo',
                {
                    id_usuario: this.usuario.id, cliente, estado, municipio
                })
                if(error){
                    this.$store.commit('alerta/addAlertas', { mensaje: mensaje, estado: http_estado, error: error })
                    // this.texto_sin_datos = error.response.data.error
                }
            }
        }
    }
};
</script>
