<template>
    <v-dialog v-model="dialogo" transition="dialog-top-transition" max-width="500" persistent>
        <template v-slot:default="dialog">
            <v-sheet class="container">
                <v-card class="mx-auto" outlined :loading="cargando">
                    <v-card-title class="grey lighten-2">
                        Crear un nuevo concepto
                        <v-spacer />
                        <v-btn @click="cerrrarDialogo(dialog)" color="error" icon>
                            <v-icon>mdi-close</v-icon>
                        </v-btn>
                    </v-card-title>
                    <v-card-subtitle class="grey lighten-2">{{ proyecto.nombre }}</v-card-subtitle>
                    <v-card-text>
                        <br>
                        <v-form v-model="valido_form" ref="form_crear_nuevo_concepto" lazy-validation>
                            <v-text-field v-model="nombre" label="Nombre del concepto" filled outlined clearable dense :rules="concepto_reglas"/>
                        </v-form>
                    </v-card-text>
                    <v-card-actions>
                        <v-btn @click="create(dialog)" color="green white--text" block :disabled="!valido_form">Crear</v-btn>
                    </v-card-actions>
                </v-card>
            </v-sheet>
        </template>
    </v-dialog>
</template>

<script>
export default {
  props:{
    dialogo: { type: Boolean, default: false }
  },
  data: () => ({
    concepto_nombre: '',
    concepto_reglas: [
        v => !!v || 'Escribe el nombre del concepto.',
        v => (v && 3 <= v.length) || 'El nombre del concepto debe tener 3 caracteres o más.'
    ],
    valido_form: true
  }),
  computed: {
    proyecto() { return this.$store.getters['proyecto/getProyecto'] },
    isProyecto() { return Boolean(Object.keys(this.proyecto).length) },
    nombre:{
        get(){ return this.concepto_nombre },
        set(nuevo_valor){ this.concepto_nombre = nuevo_valor == null ? '' : nuevo_valor }
    },
    cargando() { return this.$store.getters['proyecto/getCargando'] }
  },
  methods: {
    async create(dialog) {
        if(this.$refs.form_crear_nuevo_concepto.validate()){
            const { nombre, proyecto } = this;
            const { mensaje, estado, error, } = await this.$store.dispatch('proyecto/crearConcepto', {
                nombre: nombre,
                proyecto: proyecto.id,
            });
            this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error })
            if(!error){ this.cerrrarDialogo(dialog) }
        }
    },
    cerrrarDialogo(dialog){
      dialog.value = false
      this.$emit('emitDialogo', {dialogo: false})
      this.$refs.form_crear_nuevo_concepto.reset()
    }
  },
};
</script>
