<template>
    <v-main>
        <v-row justify="center">
            <v-col xs="12" sm="12" md="8" lg="8" xl="8">
                <v-stepper v-model="paso_formulario">
                    <v-stepper-header>
                        <v-stepper-step step="1"> Paso 1 <br> Cliente </v-stepper-step>
                        <v-divider />
                        <v-stepper-step step="2"> Paso 2 <br> Inmuebles </v-stepper-step>
                    </v-stepper-header>
                    <v-stepper-items>
                        <v-stepper-content step="1">
                            <formulario-leer-inmuebles
                                :is_cliente="is_cliente"
                                :is_clean_inmueble_verificados="is_clean_inmueble_verificados"
                                @eventoContinuar="setValidP2"
                            />
                            <br>
                            <v-btn block color="success" @click="paso_formulario=2" :disabled="!is_valido_paso_2 || is_cargando">Continuar</v-btn>
                        </v-stepper-content>
                        <v-stepper-content step="2">
                            <v-btn block color="primary" @click="paso_formulario=1">Regresar</v-btn>
                            <br>
                            <tabla-inmuebles
                                :titulo_card="cliente.nombre"
                                :isCardCabecera="true"
                                :cabecera="cabecera"
                                :inmuebles="inmuebles"
                                texto_sin_datos="Seleccione un archivo, para verificar los inmuebles."
                                :obj_grupo_btn_verificar_inmuebles="{ is_btn_borror_inmuebles_verificados: true, is_btn_verificar_inmuebles: true}"
                                @emitBorrarInmueblesVerificados="setIsCleanInmueblesVerificados"
                            />
                        </v-stepper-content>
                    </v-stepper-items>
                </v-stepper>
            </v-col>
            <v-col xs="12" sm="12" md="2" lg="2" xl="2">
                <pasos-formulario
                    :paso_formulario="paso_formulario"
                    :inmuebles="inmuebles"
                    @emitBorrarInmueblesVerificados="setIsCleanInmueblesVerificados"
                />
            </v-col>
        </v-row>
    </v-main>
</template>

<script>
import FormularioCrearInmueblesArchivo from '../../components/Inmueble/FormularioCrearMultiplesInmuebles.vue'
import TablaCrearInmueblesArchivo from '../../components/Inmueble/TablaInmuebles.vue'
import PasosFormulario from '../../components/Inmueble/FormularioPasosInstrucciones.vue'

export default {
    props:{
        is_cliente: { type: Boolean, default: true }
    },
    components: {
        'formulario-leer-inmuebles': FormularioCrearInmueblesArchivo,
        'tabla-inmuebles': TablaCrearInmueblesArchivo,
        'pasos-formulario': PasosFormulario
    },
    data:()=>({
        cabecera: [   
            { value: 'status', text: 'Status' },   
            { value: 'clave', text: 'Clave', sortable: false },
            { value: 'sirh', text: 'SIRH' },
            { value: 'nombre', text: 'Inmueble' },
            { value: 'estado', text: 'Estado' },
            { value: 'municipio', text: 'Municipio' },
            { value: 'direccion', text: 'Direccion' },      
            { value: 'accion', text: 'Act. & Eli.', align: 'center' }
        ],
        paso_formulario_now: 1,
        is_valido_continuar_paso_2: false,
        is_clean_inmueble_verificados: false
    }),
    computed:{
        cliente(){ return this.$store.getters['cliente/getCliente'] },
        inmuebles(){ return this.$store.getters['inmueble/getInmueblesVerificados'] },
        is_cargando(){ return this.$store.getters['inmueble/getCargando'] },
        paso_formulario:{
            get(){ return this.paso_formulario_now },
            set(nuevo_valor){ this.paso_formulario_now = nuevo_valor }
        },
        is_valido_paso_2:{
            get(){ return this.is_valido_continuar_paso_2 },
            set(nuevo_valor){
                this.is_valido_continuar_paso_2 = nuevo_valor
                let instruccion = { paso: 0, sub_paso: 2, instruccion: { icon: 'mdi-checkbox-blank-circle', color: 'grey' } }
                if(nuevo_valor){
                    instruccion.instruccion = { icon: 'mdi-arrow-right-bold-circle', color: 'primary' }
                }
                this.$store.commit('inmueble/updateArrayIntrucciones', instruccion)
            }
        }
  },
    methods:{
        setValidP2({validacion}){ this.is_valido_paso_2 = validacion },
        setIsCleanInmueblesVerificados(){
            this.is_clean_inmueble_verificados = !this.is_clean_inmueble_verificados
        }
    }
};
</script>
