<template>
  <v-card class="mx-auto" outlined>
    <v-card-title class="grey lighten-2">
      Formulario para asignación y verificación de trabajos.
    </v-card-title>
    <v-row class="container" justify="space-around" dense>
      <v-col xs="12" sm="12" md="4" lg="4" xl="4">
        <v-card outlined>
          <v-card-text>
              <v-form ref="form" v-model="valid" lazy-validation>
                <v-row no-gutters>
                  <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                    <v-select
                      v-model="proyecto"
                      label="Proyecto"
                      :items="proyectos"
                      :rules="[v => Boolean(Object.keys(v || {})[0]) || 'Se debe escoger un proyecto']"
                      item-text="nombre"
                      :loading="isCargando"
                      no-data-text="Proyectos no disponibles"
                      clearable
                      return-object
                      outlined
                      filled
                    >
                      <template v-slot:append-outer>
                        <v-icon @click="isDialogCrearNuevoProyecto=true" color="success">mdi-plus-circle</v-icon>
                      </template>
                  </v-select>
                  </v-col>
                  <v-col xs="12" sm="12" md="12" lg="12" xl="12">
                    <v-select
                      v-model="conceptos_seleccionados"
                      label="Conceptos"
                      :items="conceptos"
                      :rules="[(v) => !!v.length > 0 || 'Se debe elejir por lo menos un concepto']"
                      item-text="nombre"
                      return-object
                      no-data-text="No se hallaron conceptos vinculadas al proyecto."
                      :loading="isCargandoConcepto"
                      small-chips
                      multiple
                      outlined
                      clearable
                      :disabled="Object.hasOwn(proyecto, 'id') ? false : true"
                      filled
                    >
                      <template v-slot:append-outer>
                        <v-icon :disabled="Object.hasOwn(proyecto, 'id') ? false : true" @click="isDialogCrearNuevoConcepto=true" color="success">mdi-plus-circle</v-icon>
                      </template>
                    </v-select>
                  </v-col>
                </v-row>
              </v-form>
          </v-card-text>
        </v-card>
        <v-row no-gutters>
          <v-col xs="12" sm="12" md="12" lg="12" xl="12">
            <div class="container text-center">
              <v-btn
                text
                :disabled="conceptos_seleccionados.length ? false : true"
                @click="agregar">
                Asingnar todos <v-icon right>mdi-chevron-right</v-icon>
              </v-btn>
            </div>
          </v-col>
        </v-row>
      </v-col>
      <v-col xs="12" sm="12" md="8" lg="8" xl="8">
        <v-card class="mx-auto" outlined>
          <v-sheet class="container" color="primary">
            <v-slide-group v-model="tab_inmueble" mandatory show-arrows>
              <v-slide-item v-for="{inmueble} in array_trabajos_no_verificados" :key="inmueble.id" v-slot="{ active, toggle }">
                <v-btn
                  class="mx-2"
                  :input-value="active"
                  active-class="black white--text"
                  rounded
                  depressed
                  @click="toggle"
                >
                  {{ inmueble.nombre }}({{ inmueble.clave }}-{{ inmueble.sirh }})</v-btn>
              </v-slide-item>
            </v-slide-group>
          </v-sheet>
          <v-tabs-items v-model="tab_inmueble">
              <v-tab-item v-for="(inmueble, index_inmueble) in array_trabajos_no_verificados" :key="index_inmueble">
                <v-card class="mx-auto">
                  <v-card-text>
                    <v-simple-table>
                      <template v-slot:default>
                        <thead style="position: sticky; top: 0; z-index: 1; background-color: white;">
                          <tr>
                            <th style="text-align: center;">Proyecto</th>
                            <th style="text-align: center">Trabajo</th>
                            <th style="text-align: center">Eliminar</th>
                          </tr>
                        </thead>
                        <tbody v-if="inmueble.conceptos.length">
                          <tr v-for="(concepto, index_concepto) in inmueble.conceptos" :key="index_concepto">
                            <td style="text-align: center;">{{concepto.proyecto}}</td>
                            <td style="text-align: center;">{{concepto.nombre}}</td>
                            <td style="text-align: center;">
                              <v-btn color="error" icon @click="eliminarConceptoInmueble(index_inmueble, index_concepto)">
                                <v-icon>mdi-delete</v-icon>
                              </v-btn>
                            </td>
                          </tr>
                        </tbody>
                        <tbody v-else style="text-align: center;">
                          <tr>
                            <td colspan="3">Elija al menos un concepto y haga clic en 'Asignar todos'.</td>
                          </tr>
                        </tbody>
                      </template>
                    </v-simple-table>
                  </v-card-text>
                </v-card>
              </v-tab-item>
          </v-tabs-items>
        </v-card>
        <v-row no-gutters>
          <v-col xs="12" sm="12" md="12" lg="12" xl="12">
            <div class="container text-center">
            <v-btn
              text
              @click="eliminarConceptos()">
              <v-icon left>mdi-chevron-left</v-icon> Eliminar todos 
            </v-btn>
            </div>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
      <formulario-crear-nuevo-proyecto :dialog="isDialogCrearNuevoProyecto" @emitDialog="setDialogoFormularioCrearNuevoProyecto"/>
      <formulario-crear-nuevo-concepto :dialogo="isDialogCrearNuevoConcepto" @emitDialogo="setDialogoFormularioCrearNuevoConcepto"/>
  </v-card>
</template>

<script>
import FormularioCrearConcepto from '../../components/Proyecto/CrearNuevoConcepto.vue'
import FormularioCrearProyecto from '../../components/Proyecto/CrearNuevoProyecto.vue'

export default {
  data: () => ({
    valid: true,
    conceptos_seleccionados: [],
    isDialogCrearNuevoProyecto: false,
    isDialogCrearNuevoConcepto: false,
    tab_inmueble: null
  }),
  components: {
    'formulario-crear-nuevo-concepto': FormularioCrearConcepto,
    'formulario-crear-nuevo-proyecto': FormularioCrearProyecto,
  },
  computed: {
    proyectos() { return this.$store.getters['proyecto/getProyectos'] },
    proyecto: {
      get(){ return this.$store.getters['proyecto/getProyecto'] },
      async set(nuevoValor){
        if(nuevoValor){
          this.$store.dispatch('proyecto/obtenerProyecto', nuevoValor)
          this.obtenerConceptos(nuevoValor)
        } else {
          this.$store.commit('proyecto/cleanProyecto')
        }
      }
    },
    conceptos(){ return this.$store.getters['proyecto/getConceptos'] },
    inmuebles_seleccionados(){ return this.$store.getters['inmueble/getInmueblesSeleccionados'] },
    isCargando(){ return this.$store.getters['proyecto/getCargando'] },
    isCargandoConcepto(){ return this.$store.getters['proyecto/getCargandoConceptos'] },
    array_trabajos_no_verificados(){ return this.$store.getters['trabajo/getTrabajosNoVerificados'] },
    array_trabajos_verificados(){ return this.$store.getters['trabajo/getTrabajosVerificados'] },
    paso_formulario(){ return this.$store.getters['trabajo/getPasoFormulario'] }
  },
  created(){
    this.inicializarProyectos()
  },
  methods: {
    async agregar(){
      let array_trabajos_no_verif = this.array_trabajos_no_verificados.map(trabajo => ({
        inmueble: trabajo.inmueble,
        conceptos: this.conceptos_seleccionados.map(concepto=> concepto)
      }))
      this.$store.commit('trabajo/setTrabajosNoVerificados', array_trabajos_no_verif)
      // console.log(this.$refs.tabla_trabajos_no_verif.$el.clientHeight)
    },
    eliminarConceptos(){
      let array_trabajos_no_verif = this.array_trabajos_no_verificados.map(trabajo => ({
        inmueble: trabajo.inmueble,
        conceptos: []
      }))
      this.$store.commit('trabajo/setTrabajosNoVerificados', array_trabajos_no_verif)
    },
    async inicializarProyectos() { await this.$store.dispatch('proyecto/obtenerProyectos') },
    async obtenerConceptos({id}) {
      const { mensaje, estado, error } = await this.$store.dispatch('proyecto/obtenerConceptos', id)
      if (error){
        this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error })
      }
    },
    setDialogoFormularioCrearNuevoProyecto({dialog, proyecto_creado}){
      this.isDialogCrearNuevoProyecto = dialog
      if(proyecto_creado){
        this.$store.commit('proyecto/cleanConcepto')
        this.$store.commit('proyecto/cleanConceptos')
      }
    },
    setDialogoFormularioCrearNuevoConcepto({dialogo}){ this.isDialogCrearNuevoConcepto = dialogo },
    eliminarConceptoInmueble(index_inmueble, index_concepto){
      this.$store.commit('trabajo/deleteConceptoTrabajosNoVerificados', { indice_inmueble: index_inmueble, indice_concepto: index_concepto })
      this.$emit('emitFormulario', {is_valido: this.isVerificarTrabajosNoVerif(this.array_trabajos_no_verificados)})
    },
    isVerificarTrabajosNoVerif(trabajos_no_verif){
      if(trabajos_no_verif.length){
        for(const {conceptos} of trabajos_no_verif){
          if(!conceptos.length){ return false }
        }
        return true
      }
      return false
    }
  },
  watch: {
    paso_formulario(valor){
      if(valor === 2){
        const { array_trabajos_no_verificados } = this
        let arrary_inmuebles_trabajos_no_verif = []
        if(array_trabajos_no_verificados.length === 0){
          arrary_inmuebles_trabajos_no_verif = this.inmuebles_seleccionados.map(inmueble => ({
            inmueble: inmueble,
            conceptos: []
          }))
        } else {
          const array_id_inmuebles_seleccionados = this.inmuebles_seleccionados.map(inmueble => inmueble.id)
          if(array_id_inmuebles_seleccionados.length < array_trabajos_no_verificados.length){
            for(const obj_trabajo of array_trabajos_no_verificados){
              if(array_id_inmuebles_seleccionados.includes(obj_trabajo.inmueble.id)){
                arrary_inmuebles_trabajos_no_verif.push(obj_trabajo)
              }
            }
          } else {
            const array_id_trabajos_no_verif = array_trabajos_no_verificados.map(({inmueble}) => (inmueble.id))
            let array_inmuebles_trabajos_no_verif = []
            for(const inmueble of this.inmuebles_seleccionados){
              if(!array_id_trabajos_no_verif.includes(inmueble.id)){
                array_inmuebles_trabajos_no_verif.push({
                  inmueble: inmueble,
                  conceptos: []
                })
              }
            }
            arrary_inmuebles_trabajos_no_verif = array_trabajos_no_verificados.concat(array_inmuebles_trabajos_no_verif)
          }
        }
        this.$store.commit('trabajo/setTrabajosNoVerificados', arrary_inmuebles_trabajos_no_verif)
      }
    },
    array_trabajos_no_verificados(valor){
      this.$emit('emitFormulario', {is_valido: this.isVerificarTrabajosNoVerif(valor)}) 
    }
  }
};
</script>
