<template>
    <v-row no-gutters justify="center">
        <v-col cols="12" sm="8" md="8" lg="8" xl="8" xs="8">
            <v-card class="mx-auto" outlined elevation="5">
                <v-card-text>
                    <tabla-inmuebles
                        :cabecera="cabecera"
                        :inmuebles="inmuebles"
                        :isMostrarMenuInmuebles="true"
                        :texto_sin_datos="texto_sin_datos"
                        :obj_filtrar_inmueble="{ show_btn_filtrar_inmueble: true, deshabilitar_autocomplete_cliente: false }"
                        :isCardCabecera="true"
                        :is_mostrar_btn_actualizar="true"
                        @emitFiltro="inicializarInmuebles"
                    />
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>

<script>
import TablaInmuebles from '../../components/Inmueble/TablaInmuebles.vue'

export default {
    data: () => ({
        cabecera: [
            { value: 'cliente', text: 'Cliente', sortable: false },
            { value: 'clave', text: 'Clave' },
            { text: 'SIRH', value: 'sirh' },
            { value: 'nombre', text: 'Inmueble' },
            { value: 'estado', text: 'Estado' },
            { value: 'municipio', text: 'Municipio' },
            { value: 'direccion', text: 'Direccion', sortable: false },
            { value: 'accion', text: 'Act. & Eli.', sortable: false, align: 'center' }
        ],
        texto_sin_datos: ''
    }),
    components: {
        'tabla-inmuebles': TablaInmuebles,
    },
    computed: {
        inmuebles() { return this.$store.getters['inmueble/getInmuebles'] },
        cliente() { return this.$store.getters['cliente/getCliente'] }
    },
    created() {
        this.inicializarInmuebles({cliente: this.cliente, estado: {}, municipio: {}})
    },
    methods:{
        async inicializarInmuebles({cliente, estado, municipio}){
            if(Object.keys(this.cliente).length || Object.keys(estado).length || Object.keys(municipio).length){
                const { mensaje, estadoHttp, error } = await this.$store.dispatch(
                    'inmueble/listar', { cliente, estado, municipio }
                )
                if(error){
                    this.$store.commit('alerta/addAlertas', { mensaje: mensaje, estado: estadoHttp, error: error })
                    this.texto_sin_datos = error.response.data.error
                }
            } else {
                // this.$store.commit('inmueble/cleanInmuebles')
                // this.texto_sin_datos= 'Filtra los inmuebles primero por ubicación antes de listarlos.'
            }
        }
    }
};
</script>
