<script>
import TablaTrabajos from '../../components/trabajo/TablaTrabajos.vue'

export default {
    props: {
        clave_inmueble:{ type: String, default: '' }
    },
    data:()=>({
        headers: [
            { value: 'proyecto', text: 'Proyecto' },
            { value: 'concepto', text: 'Concepto' },
            { value: 'status', text: 'Status' },
            // { value: 'inmueble.municipio', text: 'Usuario en campo' },
            { value: 'creado', text: 'Creado' },
            { value: 'modificado', text: 'Modificado' },
        ],
    }),
    components: {
        'tabla-trabajos-inmueble': TablaTrabajos,
    },
    computed:{
        inmueble(){ return this.$store.getters['inmueble/getInmueble'] },
        trabajos(){ return this.$store.getters['trabajo/getTrabajos'] }
    },
    async created(){
        await this.inicializarInmueble(this.clave_inmueble)
    },
    methods: {
        async inicializarInmueble(clave_inmueble){
            await this.$store.dispatch('inmueble/recuperarInmueble', { clave_inmueble: clave_inmueble })
            const { error } = await this.$store.dispatch('trabajo/obtenerTrabajosInmueble', this.inmueble.id)
            if(error){
                this.$store.commit('trabajo/setMensaje', 'NO')
            }
        }
    }
}
</script>