<template>
  <v-card class="mx-auto" outlined max-width="500">
    <v-card-title class="grey lighten-2"> {{ Object.hasOwn(inmueble, 'id') ? 'Actualizar inmueble' : 'Crear nuevo inmueble' }}
      <v-spacer />
        <v-btn icon fab color="error" @click="cerrarDialgo({obj_creado: false})">
          <v-icon>mdi-close</v-icon>
        </v-btn>
    </v-card-title>
    <v-card-text class="mt-4">
      <v-form ref="formulario" v-model="validacion" lazy-validation>
        <v-autocomplete
          v-model="inmueble.cliente"
          label="Cliente*"
          :rules="[(v) => !!v || 'Es necesario elegir un cliente']"
          :items="clientes"
          item-text="nombre"
          return-object
          no-data-text="No se encontraron estados."
          outlined
          filled
          clearable
          dense
          :disabled="!is_autocomplete_cliente"
        />
        <v-text-field
          v-model="inmueble.nombre"
          label="Nombre*"
          :rules="true ? [(v) => !!v || 'Es necesario un nombre'] : []"
          dense
          outlined
          filled
          clearable
        />
        <v-text-field
          v-model="inmueble.clave"
          label="Clave*"
          :rules="[(v) => !!v || 'Es necesario una clave']"
          outlined
          filled
          clearable
          dense
        />
        <v-text-field
          v-model="inmueble.sirh"
          label="SIRH"
          outlined
          filled
          clearable
          dense
        />
        <v-autocomplete
          v-model="inmueble.estado"
          label="Estado*"
          :rules="[(v) => !!v || 'Es necesario elegir un Estado']"
          :items="estados"
          item-text="nombre"
          no-data-text="No se encontraron estados."
          @change="inmueble.estado ? obtenerMunicipiosDeEstado(inmueble.estado.id): $store.commit('ubicacion/clearMunicipios')"
          return-object
          outlined
          filled
          clearable
          dense
        />
        <v-autocomplete
          v-model="inmueble.municipio"
          label="Municipio*"
          :rules="[(v) => !!v || 'Es necesario elegir un Municipio']"
          :items="municipios"
          item-text="nombre"
          no-data-text="No se encontraron municipios."
          return-object
          outlined
          filled
          clearable
          dense
        />
        <!-- <v-autocomplete
          v-model="inmueble.localidad"
          label="Localidad"
          item-text="nombre"
          :items="localidades"
          return-object
          no-data-text="No se encontraron localidades."
          outlined
          filled
          clearable
          dense
        /> -->
        <v-textarea
          v-model="inmueble.direccion"
          label="Direccion"
          auto-grow
          rows="1"
          row-height="15"
          outlined
          filled
          clearable
        />
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-row>
        <v-col cols="6">
          <v-btn block @click="cerrarDialgo({obj_creado: false})" color="error">Cerrar</v-btn>
        </v-col>
        <v-col cols="6">
          <v-btn v-if="Object.hasOwn(inmueble, 'id')"
            block @click="actualizarInmueble()" :disabled="!validacion" color="warning"
          >
            Actualizar inmueble
          </v-btn>
          <v-btn v-else
            block @click="crearInmueble()" :disabled="!validacion" color="success"
          >
            Crear inmueble
          </v-btn>
        </v-col>
      </v-row>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  props: {
    dialog: {type: Boolean, default: false},
    is_autocomplete_cliente:{ type: Boolean, default: false},
  },
  data: () => ({
    validacion: false,
  }),
  computed: {
    inmueble(){ return this.$store.getters["inmueble/getInmueble"] },
    clientes(){ return this.$store.getters["cliente/getClientes"] },
    estados() { return this.$store.getters["ubicacion/getEstados"] },
    municipios() { return this.$store.getters["ubicacion/getMunicipios"]; },
    localidades() { return this.$store.getters["ubicacion/getLocalidades"]; },
  },
  created(){
    this.inmueble.cliente = this.$store.getters['cliente/getCliente']
  },
  methods: {
    async inicializarEstados() {
      await this.$store.dispatch("ubicacion/obtenerEstados");
    },
    async obtenerMunicipiosDeEstado(id_estado) {
      await this.$store.dispatch("ubicacion/obtenerMunicipiosDeEstado", id_estado);
    },
    async crearInmueble() {
      if (this.$refs.formulario.validate()) {
        const {mensaje, estado, error } = await this.$store.dispatch("inmueble/crearInmueble", this.inmueble)
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        if(!error){
          this.cerrarDialgo({obj_creado: true})
        }
      }
    },
    async actualizarInmueble() {
      if (this.$refs.formulario.validate()) {
        const {mensaje, estado, error } = await this.$store.dispatch("inmueble/actualizarInmueble", this.inmueble)
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        if(!error){
          this.cerrarDialgo({obj_creado: false})
        }
      }
    },
    cerrarDialgo({obj_creado}){
      this.$emit("eventSetDialog", { is_dialogo: false, obj_inmueble_creado: obj_creado, obj_inmueble_modificado: true })
    },
  },
};
</script>
