<template>
    <v-row dense justify="center">
        <v-col xs="12" sm="12" md="8" lg="8" xl="8">
            <v-stepper v-model="paso_formulario">
                <v-stepper-header>
                    <v-stepper-step :complete="paso_formulario > 1" step="1">
                        Paso 1 <br />
                        Inmuebles
                    </v-stepper-step>

                    <v-divider />

                    <v-stepper-step :complete="paso_formulario > 2" step="2">
                        Paso 2 <br />
                        Datos genereales
                    </v-stepper-step>

                    <v-divider />

                    <v-stepper-step step="3">
                        Paso 3 <br />
                        Asignar
                    </v-stepper-step>
                </v-stepper-header>
                <v-stepper-items>
                    <v-stepper-content step="1">
                        <tabla-inmuebles
                            :isMostrarSeleccion="true"
                            :isMostrarMenuInmuebles="false"
                            :obj_filtrar_inmueble="obj_filtrar_inmueble"
                            :cabecera="cabecera"
                            :inmuebles="inmuebles"
                            :isCardCabecera="true"
                            @emitFiltro="eventFiltro"
                            :tabla_height="350"
                        />
                        <br />
                        <v-btn
                            color="success"
                            block
                            @click="paso_formulario = 2"
                            :disabled="!is_inmuebles_seleccionados"
                        >
                            Continuar
                        </v-btn>
                    </v-stepper-content>

                    <v-stepper-content step="2">
                        <v-btn color="warning" block @click="paso_formulario = 1"> Regresar </v-btn>
                        <br />
                        <formulario-crear-trabajo @emitFormulario="setDeshabilitarBtnDos" />
                        <br />
                        <v-btn
                            color="primary"
                            @click="irPasoTres()"
                            block
                            :disabled="deshabilitar_btn_dos"
                        >
                            Continuar
                        </v-btn>
                    </v-stepper-content>
                    <v-stepper-content step="3">
                        <v-btn color="warning" block @click="paso_formulario=2"> Regresar </v-btn>
                        <br />
                        <confirmar-trabajos-verificados />
                        <br />
                    </v-stepper-content>
                </v-stepper-items>
            </v-stepper>
        </v-col>
        <v-col xs="12" sm="12" md="2" lg="2" xl="2">
            <pasos-formulario :paso_formulario="paso_formulario" @emitCancelarFormulario="aciveDialogoAlertaFormulario"></pasos-formulario>
        </v-col>
        <dialogo-alerta-formulario
            :dialogo="dialogoAlertaFormulario"
            tipo_mensaje="Formulario crear múltiples trabajos"
            mensaje="Estás a punto de borrar toda la información. ¿Estás seguro?"
            @emitConfirmacion="setDialogoAlertaFormulario"
        />
    </v-row>
</template>

<script>
import TablaInmuebles from '../../components/Inmueble/TablaInmuebles.vue'
import FormularioVerificarTrabajos from '../../components/trabajo/FormularioVerificarTrabajos.vue'
import ConfirmarTrabajosVerificados from './ConfirmarTrabajosVerificados.vue'
import AlertaFormulario from '../../alertas/AlertaFormulario.vue'
import PasosFormulario from './FormularioPasosoInstrucciones.vue'

export default {
    props: { 
        obj_filtrar_inmueble: {
            type: Object,
            default:()=>({
                show_btn_filtrar_inmueble: false, deshabilitar_autocomplete_cliente: false
            })
        },
    },
    data: () => ({
        deshabilitar_btn_dos: false,
        dialogoAlertaFormulario: false,
        cabecera: [
            { value: 'cliente', text: 'Cliente', sortable: false },
            { value: 'clave', text: 'Clave' },
            { value: 'sirh', text: 'SIRH' },
            { value: 'nombre', text: 'Inmueble' },
            { value: 'estado', text: 'Estado' },
            { value: 'municipio', text: 'Municipio' },
            { value: 'direccion', text: 'Direccion', sortable: false },
        ]
    }),
    components: {
        'tabla-inmuebles': TablaInmuebles,
        'formulario-crear-trabajo': FormularioVerificarTrabajos,
        'confirmar-trabajos-verificados': ConfirmarTrabajosVerificados,
        'dialogo-alerta-formulario': AlertaFormulario,
        'pasos-formulario': PasosFormulario
    },
    computed: {
        is_inmuebles_seleccionados(){ return this.$store.getters['inmueble/getInmueblesSeleccionados'].length ? true : false },
        cliente(){ return this.$store.getters['cliente/getCliente'] },
        inmuebles(){ return this.$store.getters['inmueble/getInmuebles'] },
        paso_formulario: {
            get(){ return this.$store.getters['trabajo/getPasoFormulario'] },
            set(nuevo_valor){
                this.$store.commit('trabajo/setPasoFormulario', nuevo_valor)
            }
        },
        array_trabajos_no_verificados(){ return this.$store.getters['trabajo/getTrabajosNoVerificados'] },
        is_trabajos_verificados(){ return this.$store.getters['trabajo/getTrabajosVerificados'].length ? true : false }
    },
    methods: {
        setDeshabilitarBtnDos({ is_valido }){
            this.deshabilitar_btn_dos = !is_valido 
            let instruccion = { paso: 1, sub_paso: 3, instruccion: { icon: 'mdi-checkbox-blank-circle', color: 'grey' } }
            if(is_valido){
                instruccion.instruccion = { icon: 'mdi-arrow-right-bold-circle', color: 'primary' }
            }
            this.$store.commit('trabajo/updateArrayIntrucciones', instruccion)
        },
        aciveDialogoAlertaFormulario(){
            this.dialogoAlertaFormulario = true
        },
        setDialogoAlertaFormulario({confirmacion, dialogo}){
            if(confirmacion){ this.cancelarAsignacion() }
            this.dialogoAlertaFormulario = dialogo
        },
        async irPasoTres(){
            if(!this.deshabilitar_btn_dos){
                this.paso_formulario = 3
                const { mensaje, error, estado } = await this.$store.dispatch('trabajo/verificarTrabajos', this.array_trabajos_no_verificados)
                this.$store.commit('trabajo/cleanTrabajosNoVerificados')
                this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error })
                if (error){
                    this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error })
                }
            }
        },
        cancelarAsignacion(){
            this.$store.commit('inmueble/cleanInmueblesSeleccionados')
            this.$store.commit('trabajo/cleanFormularioAsignarTrabajosInmuebles')
            // this.$router.push({ name: 'trabajos-cliente', params: { cliente: this.cliente.nombre } })
            this.$router.go(-1)
        },
        eventFiltro({ cliente, estado, municipio }){
            this.$emit('emitFiltro', { cliente, estado, municipio })
        },
        cambiarEsatdoInstruccion(valor){
            let instruccion_1 = { paso: 0, sub_paso: 0, instruccion: { icon: 'mdi-checkbox-blank-circle', color: 'grey' } }
            let instruccion_2 = { paso: 0, sub_paso: 1, instruccion: { icon: 'mdi-checkbox-blank-circle', color: 'grey' } }
            if(valor){
                instruccion_1.instruccion = { icon: 'mdi-check-circle', color: 'success' }
                instruccion_2.instruccion = { icon: 'mdi-arrow-right-bold-circle', color: 'primary' }
            }
            this.$store.commit('trabajo/updateArrayIntrucciones', instruccion_1)
            this.$store.commit('trabajo/updateArrayIntrucciones', instruccion_2)
        }
    },
    watch: {
        is_inmuebles_seleccionados(valor){
            this.cambiarEsatdoInstruccion(valor)
        }
    }
};
</script>
