<template>
    <v-row justify="center">
        <v-col cols="8">
            <v-card class="mx-auto">
                <v-card-text>
                    <listar-trabajos
                        :obj_filtrar="{ show_filtado_btn, array_atributos_filtro }"
                        :obj_asignar="{
                            show_asignar_btn,
                            deshabilitar: false,
                            redireccionar: { name: 'asignar-trabajos-cliente', params: { cliente: cliente.nombre } }
                        }"
                        :headers="headers"
                        @eventAplicarFiltroAtributos="inicializarTrabajosCliente"
                    />
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>

<script>
import ListarTrabajos from '../../components/trabajo/TablaTrabajos.vue'

export default {
    data: () => ({
        headers: [
            { value: 'proyecto', text: 'Proyecto' },
            { value: 'concepto', text: 'Concepto' },
            { value: 'status', text: 'Status' },
            { value: 'inmueble.cliente', text: 'Cliente' },
            { value: 'inmueble.nombre', text: 'Inmueble' },
            { value: 'inmueble.clave', align: ' d-none' },
            { value: 'inmueble', text: 'Ubicación del inmueble' },
            { value: 'inmueble.municipio.name', align: ' d-none' },
            { value: 'inmueble.estado', text: 'Usuario a cargo' },
            { value: 'inmueble.municipio', text: 'Usuario en campo' },
            { value: 'creado', text: 'Creado' },
            { value: 'modificado', text: 'Modificado' }
        ],
        show_asignar_btn: true,
        show_filtado_btn: true,
        array_atributos_filtro: [
            { text: 'Cliente', disabled: true, indice: 0 },
            { text:'Estado', indice: 1 },
            // { text: 'Fecha', indice: 2 },
            // { text: 'Campo', indice: 3 }
        ]
    }),
    components: {
        'listar-trabajos': ListarTrabajos,
    },
    computed:{
        cliente(){ return this.$store.getters['cliente/getCliente'] },
    },
    created(){
        this.inicializarTrabajosCliente({ estado: {} })
    },
    methods:{
        async inicializarTrabajosCliente({ estado }){
            const { cliente } = this
            const { error } = await this.$store.dispatch('trabajo/obtenerTrabajosCliente', {
                id_cliente: Object.hasOwn(cliente, 'id') ? cliente.id : '',
                id_estado: Object.hasOwn(estado, 'id') ? estado.id : ''
            })
            if (error) {
                await this.$store.commit('alerta/addAlertas', { error: error })
            }
        }
    }
};
</script>
