import axios from "@/api"

import { localStorage2json } from "@/utils/data/store";

// import { INICIALIZAR_FUNCIONES, LOGOUT_FUNCTION } from "@/store/constants"

export default {
  namespaced: true,

  state: {
    inmueble: {
      id: null,
      cliente: {},
      nombre: '',
      clave: '',
      sirh: '',
      estado: {},
      municipio: {},
      direccion: ''
    },
    inmuebles: [],
    inmueblesSeleccionados: [],
    inmuebles_verificados: [],
    verificarInmuebles: [],

    indice_inmueble: -1,

    numeroInmueblesCreados: 0,
    total_inmuebles_verificados: 0,

    is_dialogo_formulario_crear_nuevo_inmuble: false,

    array_intrucciones_formulario :[
      [
        { icon: 'mdi-checkbox-blank-circle', color: 'grey', text: 'Elegir un cliente.' },
        { icon: 'mdi-checkbox-blank-circle', color: 'grey', text: 'Escoger un archivo Excel.' },
        { icon: 'mdi-checkbox-blank-circle', color: 'grey', text: 'Haz clic en "Continuar".' }
      ],
      [
        { icon: 'mdi-arrow-left-bold-circle', color: 'primary', text: 'Haz clic en "Regresar" para ir al Paso 1.' },
        { icon: 'mdi-information', color: 'info', text: 'Haz clic en "Crear" o "Borrar" inmuebles.' },
        { icon: 'mdi-information', color: 'info', text: 'Haz clic en "✏️" o "🗑" para manipular inmueble.' },
        { icon: 'mdi-logout-variant', color: 'error', text: 'Haz clic en "Cancelar" para terminar.' }
      ]
    ],

    cargando: false
  },

  mutations: {
    setInmueble(state, payload) { state.inmueble = payload },
    setInmuebles(state, payload) {
      // json2localStorage("inmuebles", payload)
      state.inmuebles = payload
    },
    addInmuebles(state, payload){
      state.inmuebles.push(payload)
    },
    updateInmueblePorIndice(state, {name, property_key, sirh, province, municipality, address}){
      state.inmuebles[state.indice_inmueble].nombre = name
      state.inmuebles[state.indice_inmueble].clave = property_key
      state.inmuebles[state.indice_inmueble].sirh = sirh
      state.inmuebles[state.indice_inmueble].estado = { id: province.id, nombre: province.name }
      state.inmuebles[state.indice_inmueble].municipio = {id: municipality.id, nombre: municipality.name}
      state.inmuebles[state.indice_inmueble].direccion = address
    },
    cleanInmuebles(state) {
      // json2localStorage("inmuebles", [])
      state.inmuebles = []
    },
    cleanInmueble(state) {
      // json2localStorage("inmuebles", [])
      state.inmueble = {}
    },
    setCargando(state, payload) { state.cargando = payload },
    setInmueblesSeleccionados(state, payload) { state.inmueblesSeleccionados = payload },
    cleanInmueblesSeleccionados(state) { state. inmueblesSeleccionados = []},
    eliminarInmueble(state, indice_inmueble) { state.inmuebles.splice(indice_inmueble, 1) },
    updateInmuebleVerificadoPorIndice(state, {inmueble, indice}){
      state.inmuebles_verificados[indice].nombre = inmueble.nombre
      state.inmuebles_verificados[indice].clave = inmueble.clave
      state.inmuebles_verificados[indice].sirh = inmueble.sirh
      state.inmuebles_verificados[indice].estado = inmueble.estado
      state.inmuebles_verificados[indice].municipio = inmueble.municipio
      state.inmuebles_verificados[indice].direccion = inmueble.direccion
    },
    splitInmueblesVerificados(state, payload) { state.inmuebles_verificados.splice(payload, 1) },
    splitVerificarInmuebles(state, payload) { state.verificarInmuebles.splice(payload, 1) },
    setInmueblesVerificados(state, payload) { state.inmuebles_verificados = payload },
    setVerificarInmuebles(state, payload) { state.verificarInmuebles = payload },
    cleanInmueblesVerificados(state) { state.inmuebles_verificados = [] },
    cleanVerificarInmuebles(state) { state.verificarInmuebles = [] },
    updateInmuebleVerificado(state, {indice, inmueble}) {
      if("property_key" in inmueble){ state.inmuebles_verificados[indice].clave = inmueble.property_key[0] + "(" + state.verificarInmuebles[indice].clave +  ")" }
      if("name" in inmueble){ state.inmuebles_verificados[indice].nombre = inmueble.name[0] +"(" + state.verificarInmuebles[indice].nombre +  ")"}
      if("province" in inmueble){ state.inmuebles_verificados[indice].estado = inmueble.province[0] + "(" + state.verificarInmuebles[indice].estado +  ")"}
      if("municipality" in inmueble){state.inmuebles_verificados[indice].municipio = inmueble.municipality[0] + "(" + state.verificarInmuebles[indice].municipio +  ")"}
    },
    setNumeroInmueblesCreados(state, payload){ state.numeroInmueblesCreados = payload },
    setTotalInmueblesVerificados(state, payload){ state.total_inmuebles_verificados = payload },
    setStatusInmuebleVerificado(state, {indice, status}){
      state.inmuebles_verificados[indice].status = status
    },
    setIsDialogoFormularioCrearNuevoInmuble(state, payload){
      state.is_dialogo_formulario_crear_nuevo_inmuble = payload
    },
    setIndiceInmueble(state, payload){
      state.indice_inmueble = payload
    },
    updateArrayIntrucciones(state, {paso, sub_paso, instruccion}){
      state.array_intrucciones_formulario[paso][sub_paso].icon = instruccion.icon
      state.array_intrucciones_formulario[paso][sub_paso].color = instruccion.color
    }
  },

  actions: {
    // [INICIALIZAR_FUNCIONES]({ dispatch }) {
    //   dispatch("iniciarInmuebles");
    // },
    // [LOGOUT_FUNCTION]({ commit }) {
    //   commit("limpiarInmuebles")
    // },
    // iniciarInmuebles({ commit },) {
    //   const inmuebles = localStorage2json("inmuebles")
    //   commit("MUT_ListaInmuebles", inmuebles)
    // },
    async crearInmueble({commit}, { nombre, clave, sirh, direccion, estado, municipio, localidad, cliente }){
      try {
        commit("setCargando", true)
        const { data, status } = await axios.instance.post("inmuebles/inmueble/", {
          "name": nombre,
          "property_key": clave,
          "sirh": sirh,
          "address": direccion,
          "province": estado.id,
          "municipality": municipio.id,
          "locality": localidad,
          "client": cliente.id
        })
        const { items, message } = data
        const { id, name, property_key, address, province, municipality, locality, client } = items
        const inmueble = {
          "id": id, "nombre": name, "clave": property_key, "direccion": address, "estado": province, "municipio": municipality, "localidad": locality, "cliente": client
        }
        commit("addInmuebles", inmueble)
        commit("setCargando", false)
        return { "mensaje": message, "estado": status }
      } catch (error) {
        commit("setCargando", false)
        return { "error": error }
      }
    },
    async verificarInmuebles({ commit, dispatch }, inmuebles) {
      try {
        commit('setCargando', true)
        var inmuebles_verificar = []
        inmuebles.forEach(element => {
          inmuebles_verificar.push({
            name: element.nombre,
            property_key: element.clave,
            sirh: element.sirh,
            address: element.direccion,
            province: element.estado,
            municipality: element.municipio,
            locality: element.localidad,
            client: element.cliente.id,
          })
        })
        const { data, status } = await axios.instance.post('inmuebles/inmueble/verificacion/', { properties :inmuebles_verificar})
        const { items, message } = data
        let list_inmuebles = []
        for(let inmueble of items){
          list_inmuebles.push({
            status: await dispatch('isValidoInmueble', inmueble),
            nombre: inmueble.name,
            clave: inmueble.property_key,
            sirh: inmueble.sirh,
            cliente: inmueble.client ? { id: inmueble.client.id, nombre: inmueble.client.name } : null,
            estado: inmueble.province ? { id: inmueble.province.id, nombre: inmueble.province.name }: null,
            municipio: inmueble.municipality ? { id: inmueble.municipality.id, nombre: inmueble.municipality.name } : null,
            localidad: inmueble.locality ? { id: inmueble.locality.id, nombre: inmueble.locality.name } : null,
            direccion: inmueble.address
          })
        }
        commit('setInmueblesVerificados', list_inmuebles)
        commit('setCargando', false)
        return { mensaje: message, estado: status, error: null }
      } catch (error) {
        commit('setCargando', false)
        return { error: error }
      }
    },
    async crearMultiplesInmuebles({commit, state}, inmuebles) {
      try {
        commit("setCargando", true)
        var inmuebless = []
        inmuebles.forEach(inmueble => {
          inmuebless.push({
            "name": inmueble.nombre,
            "property_key": inmueble.clave,
            "sirh": inmueble.sirh,
            "address": inmueble.direccion,
            "province": inmueble.estado ? inmueble.estado.id : null,
            "municipality": inmueble.municipio ? inmueble.municipio.id : null,
            "locality": inmueble.localidad ? inmueble.localidad.id : null,
            "client": inmueble.cliente ? inmueble.cliente.id : null,
          })
        })
        commit("setTotalInmueblesVerificados", inmuebles.length)
        const { data, status } = await axios.instance.post("inmuebles/inmueble/confirmacion/",
        { "properties": inmuebless }
        )
        var indice = 0
        const { items, message } = data
        for(const inmueble of items){
          if ("error" in inmueble){
            commit("updateInmuebleVerificado", {indice: indice, inmueble: inmueble.error})
            commit("setStatusInmuebleVerificado", {inmueble: state.inmuebles_verificados[indice], indice: indice})
            indice = indice + 1
          } else {
            commit("splitInmueblesVerificados", indice)
            commit("splitVerificarInmuebles", indice)
          }
        }
        commit("setNumeroInmueblesCreados", items.length - indice)
        commit("setCargando", false)
        return { "mensaje": message, "estado": status }
      } catch (error) {
        commit("setCargando", false)
        return { "error": error }
      }
    },
    async actualizarInmueble({commit}, {id, cliente, nombre, clave, sirh, estado, municipio, direccion}){
      try {
        commit('setCargando', true)
        const inmueble = {
          client: cliente.id,
          name: nombre,
          property_key: clave,
          sirh: sirh,
          province: estado.id,
          municipality: municipio.id,
          address: direccion
        }
        const { data, status } = await axios.instance.put(`inmuebles/inmueble/${id}/`, inmueble)
        const { items, message } = data
        commit('updateInmueblePorIndice', items)
        commit('setCargando', false)
        return { mensaje: message, estado: status }
      } catch (error) {
        commit('setCargando', false)
        return { error: error }
      }
    },
    async recuperarInmueble({commit}, clave){
      try {
        commit('setCargando', true)
        const parametros = Object.hasOwn(clave, 'id_inmueble') ? `${clave.id_inmueble}/` : `?clave=${clave.clave_inmueble}`
        const { data, status } = await axios.instance.get('inmuebles/inmueble/'.concat('', parametros))
        const { items, message } = data
        const { id, name, property_key, sirh, address, province, municipality, locality, client } = items
        const inmueble = {
          id: id,
          clave: property_key,
          sirh: sirh,
          nombre: name,
          estado: {
            id: province.id,
            nombre: province.name,
            usuario_cargo: province.users_charge
          },
          municipio: {
            id: municipality.id,
            nombre: municipality.name,
            usuario_campo: municipality.users_field
          },
          localidad: locality,
          direccion: address,
          cliente: typeof client === 'object' ? { id: client.id, nombre: client.name } : client
        }
        commit('setInmueble', inmueble)
        commit('setCargando', false)
        return { mensaje: message, status: status}
      } catch (error) {
        commit('setCargando', false)
        return { error: error }
      }
    },
    async listarInmueblesUsuarioCargo({ commit }, { id_usuario, cliente, estado, municipio }){
      try {
        commit('setCargando', true)
        commit('cleanInmuebles')
        let inmuebles = []
        const url = `cliente=${Object.hasOwn(cliente, 'id') ? cliente.id : ''}&estado=${Object.hasOwn(estado, 'id') ? estado.id : ''}&municipio=${Object.hasOwn(municipio, 'id') ? municipio.id : ''}`
        const { data, status } = await axios.instance.get(`inmuebles/cargo/${id_usuario}/`.concat('?', url))
        const { items, message } = data
        items.forEach(inmueble => {
          inmuebles.push({
            id: inmueble.id,
            clave: inmueble.property_key,
            sirh: inmueble.sirh,
            nombre: inmueble.name,
            estado: inmueble.province,
            municipio: inmueble.municipality,
            localidad: inmueble.locality,
            direccion: inmueble.address,
            cliente: inmueble.client
          })
        })
        commit('setInmuebles', inmuebles)
        commit('setCargando', false)
        return { mensaje: message, http_estado: status }
      } catch (error) {
        commit('setCargando', false)
        return { error: error }
      }
    },
    async listar({commit}, {cliente, estado, municipio}){
      try {
        commit('setCargando', true)
        commit('cleanInmuebles')
        const url = `clientes=${Object.hasOwn(cliente, 'id') ? cliente.id : ''}&estado=${Object.hasOwn(estado, 'id') ? estado.id : ''}&municipio=${Object.hasOwn(municipio, 'id') ? municipio.id : ''}&localidad=''`
        const { data, status } = await axios.instance.get('inmuebles/inmueble/'.concat('?', url),
          { headers: { Authorization : `Bearer ${localStorage2json('token')}` } }
        )
        const { items, message } = data
        const inmuebles = []
        items.forEach(inmueble => {
          inmuebles.push({
            id: inmueble.id,
            clave: inmueble.property_key,
            sirh: inmueble.sirh,
            nombre: inmueble.name,
            estado: inmueble.province.name,
            municipio: inmueble.municipality.name,
            localidad: inmueble.locality,
            direccion: inmueble.address,
            cliente: inmueble.client
          })
        })
        commit('setInmuebles', inmuebles)
        commit('setCargando', false)
        return { mensaje: message, estado: status }
      } catch (error) {
        commit('setCargando', false)
        return { error: error }
      }
    },
    async eliminarInmueble({ state, commit }, id_inmueble){
      try {
        commit('setCargando', true)
        const { data, status } = await axios.instance.delete(`inmuebles/inmueble/${id_inmueble}/`)
        const { message } = data
        commit('eliminarInmueble', state.indice_inmueble)
        commit('setCargando', false)
        return { mensaje: message, estado: status }
      } catch (error) {
        commit('setCargando', false)
        return { error: error }
      }
    },
    async setStatusInmuebleVerificado({commit, dispatch}, {inmueble, indice}){
      const inmueble_valido = {
        name: inmueble.nombre,
        province: inmueble.estado,
        municipality: inmueble.municipio,
        client: inmueble.cliente,
        property_key: inmueble.clave
      }
      const status = await dispatch("isValidoInmueble", inmueble_valido)
      commit("setStatusInmuebleVerificado", {indice: indice, status: status })
    },
    isValidoInmueble(context, {name, province, municipality, client, property_key}){
      let requerido = /Este campo es requerido\.\([^)]*\)/i
      let clave_existe = /Ya existe Inmueble con este Clave\.\([^)]*\)/i
      let clave_caracteres = /Asegúrese de que este campo no tenga más de 10 caracteres\.\([^)]*\)/i
      let nulo = /Este campo no puede ser nulo\.\([^)]*\)/i
      if(requerido.test(name) || name === null) { return false }
      if(property_key === null || requerido.test(property_key) || clave_existe.test(property_key) || clave_caracteres.test(property_key) || nulo.test(property_key)) { return false }
      if(province === null  || requerido.test(province) || nulo.test(province)) { return false }
      if(municipality === null || requerido.test(municipality) || nulo.test(municipality)){return false}
      if(client === null|| requerido.test(client) || nulo.test(client)){ return false }
      return true
    },
  },
  getters: {
    getInmuebles:(state) => state.inmuebles,
    getInmueble:(state) => state.inmueble,
    getCargando:(state) => state.cargando,
    getInmueblesSeleccionados:(state) => state.inmueblesSeleccionados,
    getInmueblesVerificados:(state) => state.inmuebles_verificados,
    getVerificarInmuebles:(state) => state.verificarInmuebles,
    getNumeroInmueblesCreados:(state) => state.numeroInmueblesCreados,
    getTotalInmueblesVerificados:(state) => state.total_inmuebles_verificados,
    getIsFormularioInmuble:(state)=>state.is_dialogo_formulario_crear_nuevo_inmuble,
    getIndiceInmueble:(state) => state.indice_inmueble,
    getArrayIntruccionesFormulario:(state) => state.array_intrucciones_formulario
  }
}