<template>
    <v-row sm="10" md="10" lg="10" xl="10">
        <asignar-trabajos
            :obj_filtrar_inmueble="{ show_btn_filtrar_inmueble, deshabilitar_autocomplete_cliente }"
            @emitFiltro="inicializarInmuebles"
        />
    </v-row>
</template>

<script>
import AsignarTrabajos from '../../components/trabajo/AsignarTrabajos.vue'  

export default {
    components: {
        'asignar-trabajos': AsignarTrabajos
    },
    data:()=>({
        show_btn_filtrar_inmueble: true,
        deshabilitar_autocomplete_cliente: false
    }),
    computed:{
        cliente(){ return this.$store.getters['cliente/getCliente'] }
    },
    created() {
        this.inicializarInmuebles({cliente: this.cliente, estado: {}, municipio: {}})
    },
    methods:{
        async inicializarInmuebles({cliente, estado, municipio}){
            if(Object.keys(this.cliente).length || Object.keys(estado).length || Object.keys(municipio).length){
                this.$store.commit('inmueble/cleanInmueblesSeleccionados')
                const { mensaje, estadoHttp, error } = await this.$store.dispatch(
                    'inmueble/listar', { cliente, estado, municipio }
                )
                if(error){
                    this.$store.commit('alerta/addAlertas', { mensaje: mensaje, estado: estadoHttp, error: error })
                }
            }
        }
    }
}
</script>