<template>
  <v-dialog v-model="dialogo" scrollable max-width="400">
    <template v-slot:activator="{ on, attrs }">
      <v-btn v-on="on" v-bind="attrs" color="primary">
        <v-icon left>mdi-filter-variant</v-icon> Filtros
      </v-btn>
    </template>
    <v-card class="mx-auto">
      <v-card-title class="grey lighten-2">
        <v-btn icon color="primary" @click="dialogo = false" x-large>
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
        <v-spacer />
        <v-btn icon color="primary" @click="cleanAll()" x-large><v-icon>mdi-eraser</v-icon></v-btn>
      </v-card-title>
      <v-card-text class="pt-2">
        <v-autocomplete
          v-model="cliente"
          label="Clientes"
          outlined
          :items="clientes"
          :disabled="!isClientes || !deshabilitar_autocomplete_cliente" 
          item-text="nombre"
          return-object
          :loading="isCargandoCliente"
          clearable
        />
        <v-autocomplete
          v-model="estado"
          label="Estado"
          outlined
          :items="estados"
          :disabled="isEstados"
          item-text="nombre"
          return-object
          :loading="isCargandoEstados"
          clearable
        />
        <v-autocomplete
          v-model="municipio"
          label="Municipio"
          outlined
          :items="municipios"
          :disabled="isMunicipios"
          item-text="nombre"
          :loading="isCargandoMunuicipios"
          clearable
          return-object
        />
      </v-card-text>
      <v-card-actions>
        <v-btn block color="primary" @click="buscarInmueblesTerritorio()">Ver Inmuebles</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    deshabilitar_autocomplete_cliente: { type: Boolean, default: false }
  },
  data: () => ({
    dialogo: false,
    localidad: null,
  }),
  computed: {
    clientes() { return this.$store.getters['cliente/getClientes'] },
    cliente: {
      get(){ return this.$store.getters['cliente/getCliente'] },
      set(nuevoValor) {
        this.$store.commit('cliente/setCliente', nuevoValor === null ? {} : nuevoValor)
      }
    },
    isClientes() { return this.clientes.length ? true : false },
    isCargandoCliente() {return this.$store.getters['cliente/getCargando'] },
    estados() { return this.$store.getters['ubicacion/getEstados'] },
    estado: {
      get() { return this.$store.getters['ubicacion/getEstado'] },
      async set(nuevoValor) {
        if (nuevoValor) {
          await this.$store.dispatch('ubicacion/obtenerEstado', nuevoValor)
          await this.obtenerMunicipiosEstado()
        } else {
          this.$store.commit('ubicacion/clearEstado')
          this.$store.commit('ubicacion/clearAllMunicipio')
        }
      },
    },
    isEstados() { return this.estados.length ? false : true },
    isCargandoEstados() { return this.$store.getters['ubicacion/getCargandoEstados'] },
    municipios() { return this.$store.getters['ubicacion/getMunicipios'] },
    municipio: {
      get() { return this.$store.getters['ubicacion/getMunicipio'] },
      async set(nuevoValor) {
        if(nuevoValor){
          await this.$store.dispatch('ubicacion/obtenerMunicipio', nuevoValor)
        } else {
          this.$store.commit('ubicacion/clearMunicipio')
        }
      },
    },
    isMunicipios() { return this.municipios.length ? false : true },
    isCargandoMunuicipios() { return this.$store.getters['ubicacion/getCargandoMunicipios'] }
  },
  created() {
    this.inicializarEstados()
  },
  methods: {
    async inicializarEstados() {
      await this.$store.dispatch('ubicacion/obtenerEstados')
    },
    async obtenerMunicipiosEstado() {
      const {cliente, estado} = this
      const { id } = cliente
      await this.$store.dispatch(
        'ubicacion/obtenerMunicipiosDeInmueblesExistentes', { cliente: id, estado }
      )
    },
    async buscarInmueblesTerritorio() {
      const { cliente, estado, municipio } = this
      this.$emit('emitFiltro', { cliente: cliente, estado: estado, municipio: municipio })
    },
    cleanAll(){
      this.$store.commit('ubicacion/clearEstado')
      this.$store.commit('ubicacion/clearMunicipio')
      this.$store.commit('ubicacion/clearLocalidad')
    }
  },
};
</script>