        <v-stepper v-model="paso_formulario" elevation="0">
            <v-stepper-items>
                <v-stepper-content step="1">
                    <table>
                        <tr>
                            <td style="width: 20%;"><v-icon :color="array_instrucciones[0][0].color">{{array_instrucciones[0][0].icon}}</v-icon></td>
                            <td style="width: 80%;">{{array_instrucciones[0][0].text}}</td>
                        </tr>
                        <tr>
                            <td><v-icon :color="array_instrucciones[0][1].color">{{array_instrucciones[0][1].icon}}</v-icon></td>
                            <td>{{array_instrucciones[0][1].text}}</td>
                        </tr>
                        <tr>
                            <td><v-icon :color="array_instrucciones[0][2].color">{{array_instrucciones[0][2].icon}}</v-icon></td>
                            <td>{{array_instrucciones[0][2].text}}</td>
                        </tr>
                    </table>
                </v-stepper-content>
                <v-stepper-content step="2">
                    <table>
                        <tr v-if="!inmuebles.length">
                            <td>
                                <v-icon :color="array_instrucciones[1][0].color">{{array_instrucciones[1][0].icon}}</v-icon>
                            </td>
                            <td>
                                {{array_instrucciones[1][0].text}}
                            </td>
                        </tr>
                        <tr v-if="inmuebles.length">
                            <td>
                                <v-icon :color="array_instrucciones[1][1].color">{{array_instrucciones[1][1].icon}}</v-icon>
                            </td>
                            <td>
                                {{array_instrucciones[1][1].text}}
                            </td>
                        </tr>
                        <tr v-if="inmuebles.length">
                            <td>
                                <v-icon :color="array_instrucciones[1][2].color">{{array_instrucciones[1][2].icon}}</v-icon>
                            </td>
                            <td>
                                {{array_instrucciones[1][2].text}}
