<script>
export default{
    props: {
        paso_formulario: { type: Number, default: 1 },
        inmuebles: { type: Array, default:()=>([]) }
    },
    computed: {
        array_instrucciones(){ return this.$store.getters['inmueble/getArrayIntruccionesFormulario'] }
    },
    methods:{
        salirFormularioCrearMultiplesInmuebles(){
            this.$store.commit('inmueble/cleanInmueblesVerificados')
            this.$store.commit('inmueble/cleanVerificarInmuebles')
            this.$emit('emitBorrarInmueblesVerificados')
            this.$router.go(-1)
        }
    }
}
</script>
