<template>
    <tabla-inmubles
        :cabecera="cabecera"
        :inmuebles="inmuebles"
        :texto_sin_datos="texto_sin_datos"
        :isMostrarMenuInmuebles="true"
        :ObjFiltrar="{isBtnFiltrar: true, isAutocompleteCliente: true}"
        :isCardCabecera="true"
        :is_mostrar_btn_actualizar="true"
        @emitFiltro="eventBuscarInmueblesFiltro"
    />
</template>

<script>
import TablaInmuebles from '../../components/Inmueble/TablaInmuebles.vue'

export default {
    data:() => ({
        cabecera: [
            { value: 'cliente', text: 'Cliente', sortable: false },
            { value: 'clave', text: 'Clave' },
            { value: 'sirh', text: 'SIRH' },
            { value: 'nombre', text: 'Inmueble' },
            { value: 'estado', text: 'Estado' },
            { value: 'municipio', text: 'Municipio' },
            { value: 'direccion', text: 'Direccion', sortable: false },
            { value: 'accion', text: 'Act. & Eli.', align: 'center' }
        ],
        texto_sin_datos: 'Filtra los inmuebles primero por cliente o ubicación antes de listarlos.'
    }),
    components: {
        'tabla-inmubles': TablaInmuebles
    },
    computed: {
        inmuebles() { return this.$store.getters['inmueble/getInmuebles'] }
    },
    methods: {
        async eventBuscarInmueblesFiltro({ cliente, estado, municipio }){
            if(Object.keys(cliente).length || Object.keys(estado).length || Object.keys(municipio).length){
                const { mensaje, estadoHttp, error } = await this.$store.dispatch(
                    'inmueble/listar', { cliente, estado, municipio }
                )
                if(error){
                    this.$store.commit('alerta/addAlertas', { mensaje: mensaje, estado: estadoHttp, error: error })
                    this.texto_sin_datos = error.response.data.error
                }
            } else {
                this.$store.commit('inmueble/cleanInmuebles')
                this.texto_sin_datos= 'Filtra los inmuebles primero por cliente o ubicación antes de listarlos.'
            }
        }
    }
};
</script>