<template>
    <v-app id="inspire">
        <v-main class="grey">
            <v-container fluid fill-height>
                <v-layout align-center justify-center>
                    <v-flex xs12 sm8 md4>
                        <v-card lass="mx-auto" elevation="5" :loading="loading">
                            <v-card-title class="grey lighten-2">
                                Iniciar sesión
                            </v-card-title>
                            <v-card-text class="pt-5">
                                <v-text-field
                                    v-model="cuenta"
                                    label="Nombre de usuario o correo"
                                    type="text"
                                    outlined
                                />
                                <v-text-field
                                    v-model="password"
                                    label="Contraseña"
                                    :type="valor ? 'password' : 'text'"
                                    v-on:keyup.enter="login()"
                                    :append-icon="valor ? 'mdi-eye' : 'mdi-eye-off'"
                                    @click:append="() => (valor = !valor)"
                                    outlined
                                />
                                <div class="p">
                                    <a href="#">¿Olvidaste tu contraseña?</a>
                                </div>
                            </v-card-text>
                            <v-card-actions>
                                <v-btn color="primary" @click="login()" block> Iniciar sesión </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-main>
    </v-app>
</template>

<script>
export default {
    name: "Login",
    data: () => ({
        cuenta: null,
        email: null,
        nombre_usuario: null,
        password: null,
        loading: false,
        valor: String
    }),
    computed: {
        nombreUsuario() { return this.$store.getters['MOD_autenticacion/getNombreUsuario'] },
        privilegios(){ return this.$store.getters['MOD_autenticacion/getPrivilegios'] }
    },
    methods: {
        async login() {
            const { email, nombre_usuario, password } = this
            this.loading = true
            const { error, estado, mensaje } = await this.$store.dispatch('MOD_autenticacion/login', {
                correo: email,
                nombreUsuario: nombre_usuario,
                password: password,
            })
            this.loading = false
            this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error })
            if (!error) {
                const { nombreUsuario } = this
                this.$router.push({ name: 'trabajos-coordinador', params: { nombre_usuario: nombreUsuario } })
            }
        },
        validarCuenta(cuenta) {
            if (/^\w+([/.-]?\w+)*@\w+([/.-]?\w+)*(\.\w{2,4})+$/.test(cuenta)) {
                this.email = cuenta
                this.nombre_usuario = null
            } else {
                this.email = null
                this.nombre_usuario = cuenta
            }
        }
    },
    watch: {
        cuenta: function() {
            this.validarCuenta(this.cuenta)
        }
    }
};
</script>
