<template>
    <v-card class="mx-auto" outlined>
        <v-sheet class="container" color="primary">
            <v-slide-group v-model="tab_inmueble" mandatory show-arrows>
                <v-slide-item v-for="{inmueble} in trabajos_verificados" :key="inmueble.id" v-slot="{ active, toggle }">
                    <v-btn
                        class="mx-2"
                        :input-value="active"
                        active-class="black white--text"
                        rounded
                        depressed
                        @click="toggle"
                    >
                        {{ inmueble.name }}({{ inmueble.property_key }}-{{ inmueble.sirh }})
                    </v-btn>
                </v-slide-item>
            </v-slide-group>
        </v-sheet>
        <v-card-title v-if="trabajos_verificados.length">
            <v-spacer></v-spacer> <v-btn color="success" @click="crearTrabajosVerificados">Crear trabajos</v-btn>
        </v-card-title>
        <v-tabs-items v-model="tab_inmueble">
            <v-tab-item v-for="({inmueble, conceptos}, index_inmueble) in trabajos_verificados" :key="index_inmueble">
                <v-simple-table>
                    <template v-slot:top>
                        {{ inmueble.province }}
                        {{ inmueble.municipality }}
                    </template>
                    <template v-slot:default>
                        <thead style="position: sticky; top: 0; z-index: 1; background-color: white;">
                            <tr>
                            <th style="text-align: center;">Trabajo</th>
                            <th style="text-align: center">Mensaje</th>
                            <th style="text-align: center;">Confirmacion</th>
                            <th style="text-align: center">Eliminar</th>
                            </tr>
                        </thead>
                        <tbody v-if="conceptos.length">
                            <tr v-for="({concept, message, confirmation}, index_concepto) in conceptos" :key="index_concepto">
                            <td style="text-align: center;">{{concept.name}} (<b>{{concept.project}}</b>)</td>
                            <td style="text-align: center;">{{message}}</td>
                            <td style="text-align: center;">
                                <v-switch :input-value="confirmation" inset color="success" disabled></v-switch>
                            </td>
                            <td style="text-align: center;">
                                <v-btn color="error" icon @click="eliminarConceptoInmueble(index_inmueble, index_concepto)">
                                    <v-icon large>mdi-delete</v-icon>
                                </v-btn>
                            </td>
                            </tr>
                        </tbody>
                        <tbody v-else style="text-align: center;">
                            <tr>
                                <td colspan="3">Elija al menos un concepto y haga clic en 'Asignar todos'.</td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </v-tab-item>
        </v-tabs-items>
        <v-card-text v-if="!trabajos_verificados.length">
            <v-row>
                <v-col cols="12"><v-btn block color="primary" @click="irPasoUno()">Continuar asignado trabajo a los inmuebles (Paso 1).</v-btn></v-col>
                <v-col cols="12"><v-btn block color="warning" @click="salirFormulario()">Ir a ver trabajos</v-btn></v-col>
            </v-row>
        </v-card-text>
    </v-card>
</template>
<script>
// import confirmacion from "../alertas/Confirmar.vue"
export default {
    data:()=> ({
        tab_inmueble: null
    }),
    components: {
        // "confirmar": confirmacion,
    },
  computed: {
    trabajos_verificados(){ return this.$store.getters['trabajo/getTrabajosVerificados'] }
  },
    methods: {
        eliminarConceptoInmueble(index_inmueble, index_concepto){
            this.$store.commit('trabajo/spliceConceptoInmueble', {indice_inmueble:index_inmueble, indice_concepto: index_concepto})
        },
        async crearTrabajosVerificados(){
            const { error, estado, mensaje } = await this.$store.dispatch('trabajo/crearTrabajosVerificados', this.trabajos_verificados)
            if(!error){
                this.$store.commit('trabajo/cleanTrabajosVerificados')
            }
            this.$store.commit('alerta/addAlertas', {estado: estado, mensaje: mensaje, error: error})
        },
        irPasoUno(){
            this.$store.commit('trabajo/cleanFormularioAsignarTrabajosInmuebles')
        },
        salirFormulario(){
            this.$router.go(-1)
        }
    }
};
</script>
