<template>
  <v-card class="mx-auto" outlined :loading="isCargando" :disabled="isCargando">
    <v-card-title v-if="isCardCabecera" class="grey lighten-2">
      {{titulo_card}}
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Buscar inmueble"
        single-line
        hide-details
        clearable
        dense
        filled
        outlined
        rounded
        class="white"
      />
    </v-card-title>
    <v-card-subtitle v-if="isCardCabecera" class="grey lighten-2">
      <span v-if="seleccionados.length">
        Seleccionados {{ seleccionados.length }} de {{ inmuebles.length }}
      </span>
      <span v-else>
        {{ inmuebles.length == 1 ? `${inmuebles.length} Inmueble` : `${inmuebles.length} Inmuebles` }}
      </span>
    </v-card-subtitle>
    <v-card-title v-if="!isCardCabecera">
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Buscar inmueble"
        single-line
        hide-details
        clearable
        dense
        filled
        outlined
        rounded
        class="white"
      />
    </v-card-title>
    <v-card-text>
      <v-responsive class="overflow-y-auto">
        <v-data-table
          v-model="seleccionados"
          @pagination="setPageText"
          :headers="cabecera"
          :items="inmuebles"
          item-key="id"
          :no-data-text="texto_sin_datos"
          no-results-text="No se encontraron registros coincidentes"
          :loading="isCargando"
          loading-text="Cargando... por favor espere"
          :items-per-page="10"
          :footer-props="{
            itemsPerPageText: 'Filas por página',
            itemsPerPageAllText: 'Todos',
            pageText: pageText,
          }"
          :show-select="isMostrarSeleccion"
          :search="search"
          class="elevation-5"
          :height="tabla_height"
        >
          <template v-slot:top >
            <v-toolbar flat>
              <v-spacer />
              <v-btn
                v-if="verificarInmuebles.length && obj_grupo_btn_verificar_inmuebles.is_btn_borror_inmuebles_verificados"
                color="warning"
                @click="clearInmueblesVerificados()"
              >
                <v-icon left>mdi-eraser-variant</v-icon>Borrar inmuebles
              </v-btn>
              <v-btn
                v-if="verificarInmuebles.length && obj_grupo_btn_verificar_inmuebles.is_btn_verificar_inmuebles"
                class="ma-5"
                color="success"
                @click="crearMultipleInmuebles()"
              >
                <v-icon left>mdi-cloud-upload</v-icon>Crear inmuebles
              </v-btn>
              <v-menu v-if="isMostrarMenuInmuebles" left offset-y :close-on-content-click="false">
                <template v-slot:activator="{ attrs, on }">
                  <v-btn color="teal" class="ma-5" v-bind="attrs" v-on="on" dark>
                    Añadir inmueble <v-icon right>{{attrs["aria-expanded"] === "true" ? "mdi-menu-up" : "mdi-menu-down"}}</v-icon>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-item @click="setDialog({is_dialogo:true})">
                    <v-list-item-title>Crear nuevo inmueble</v-list-item-title>
                    <v-dialog v-model="is_formulario_crear_inmueble" width="500">
                      <crear-inmueble :is_autocomplete_cliente="obj_filtrar_inmueble.isAutocompleteCliente" @eventSetDialog="setDialog" />
                    </v-dialog>
                  </v-list-item>
                  <v-list-item link @click="irCrearMultiplesInmuebles(obj_filtrar_inmueble)">
                    <v-list-item-title>
                      Cargar archivo
                    </v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
              <filtrado-inmueble v-if="obj_filtrar_inmueble.show_btn_filtrar_inmueble"
                :deshabilitar_autocomplete_cliente="obj_filtrar_inmueble.deshabilitar_autocomplete_cliente"
                @emitFiltro="eventFiltro"
              />
              <v-btn v-if="is_mostrar_btn_actualizar" class="ma-5" color="info" @click="eventActualizar">
                <v-icon>mdi-reload</v-icon>
              </v-btn>
              <v-dialog v-model="is_formulario_modeficar_inmueble" max-width="500" >
                <actuliza-inmueble @eventSetDialog="setDialogModificarInmueble"/>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:item.status="{item}">
            <v-icon :color="item.status ? 'success' : 'red'">
              {{item.status ? "mdi-checkbox-marked-circle-outline": "mdi-close-circle-outline"}}
            </v-icon>
          </template>
          <template v-slot:item.sirh="{item}">
            <span v-if="item.sirh">
              {{ item.sirh }}
            </span>
            <span v-else><v-chip color="primary">No asignado</v-chip></span>
          </template>
          <template v-slot:item.estado="{item}">
            <span v-if="item.estado === null"> <!-- Condición para verífica inmueble, en caso de que no encuentre la ubicación. -->
              <v-chip color="error" outlined>
                No se encontró el estado <br> {{ verificarInmuebles[inmuebles.indexOf(item)].estado}}
              </v-chip>
            </span>
            <span v-else>{{ typeof item.estado === "string" ? item.estado : item.estado.nombre }}</span>
          </template>
          <template v-slot:item.municipio="{item}">
            <span v-if="item.municipio === null"> <!-- Condición para verífica inmueble, en caso de que no encuentre la ubicación. -->
              <v-chip color="error" outlined> No se encontró <br> {{verificarInmuebles[inmuebles.indexOf(item)].municipio}} </v-chip>
            </span>
            <span v-else>{{ typeof item.municipio === "string" ? item.municipio : item.municipio.nombre }}</span>
          </template>
          <!-- <template v-slot:item.localidad="{item}">
            <v-chip v-if="item.localidad === null"> No encontrado</v-chip>
            <span v-else>{{ typeof item.localidad === "string" ? item.localidad : item.localidad.nombre }}</span>
          </template> -->
          <template v-slot:item.accion="{item}">
            <v-row no-gutters>
              <v-col cols="6">
                <v-btn icon color="info" @click="setInmueble(item)">
                  <v-icon>mdi-pencil</v-icon>
                </v-btn>
              </v-col>
              <v-col cols="6">
                <confirmar
                  :mensaje='`Estás a punto de eliminar el inmueble "${item.nombre}". ¿Estás seguro?`'
                  :indice="inmuebles.indexOf(item)"
                  :id="item.id"
                  tipo_pago="¿Eliminar inmueble?"
                  @emitConfirmacion="eliminarInmueble"
                  icon="mdi-delete"
                  color="error"
                />
              </v-col>
            </v-row>
          </template>
          <template  v-slot:item.acciones="{item}">
            <v-menu left offset-y :close-on-content-click="false">
              <template v-slot:activator="{ attrs, on }">
                <v-btn color="teal" class="ma-5" v-bind="attrs" v-on="on" dark>
                  Ir a <v-icon right>{{attrs["aria-expanded"] === "true" ? "mdi-menu-up" : "mdi-menu-down"}}</v-icon>
                </v-btn>
              </template>
              <v-list>
                <v-list-item @click="$router.push({ name: 'trabajos-inmueble-coordinador', params: { clave_inmueble: item.clave} })">
                  <v-list-item-title>Ver trabajos</v-list-item-title>
                </v-list-item>
                <v-list-item>
                  <v-list-item-title>
                    Actualizar inmuebles
                  </v-list-item-title>
                </v-list-item>
                <v-list-item>
                  <v-list-item-title>
                    Eliminar inmueble
                  </v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
          </template>
        </v-data-table>
      </v-responsive>
    </v-card-text>
  </v-card>
</template>

<script>
import FormularioCrearInmueble from  '../../components/Inmueble/FormularioCrearNuevoInmueble.vue'
import Filtrado from '../../components/Inmueble/Filtrado.vue'
import Confirmar from '../../alertas/Confirmar.vue'
export default {
  props: {
    isMostrarSeleccion: { type: Boolean, default: false },
    isMostrarMenuInmuebles: { type: Boolean, default: false },
    obj_filtrar_inmueble: { type: Object, default: () => ({ show_btn_filtrar_inmueble: false, deshabilitar_autocomplete_cliente: false }) },
    cabecera:{ type: Array, default: () => []},
    inmuebles: { type: Array, default: () => []},
    texto_sin_datos:{ type: String, default: 'No hay inmuebles disponibles' },
    isCardCabecera: { type: Boolean, default: false },
    titulo_card: { type: String, default: 'Inmuebles' },
    obj_grupo_btn_verificar_inmuebles: { type: Object, default:() => ({ is_btn_borror_inmuebles_verificados: false, is_btn_verificar_inmuebles: false }) },
    is_mostrar_btn_actualizar:{ type: Boolean, default: false },
    tabla_height: { type: Number, default: 450 }
  },
  data:()=>({
    search: '',
    pageText: 'p - Tp de Fp',
    is_formulario_crear_inmueble: false,
    is_formulario_modeficar_inmueble: false
  }),
  components:{
    'crear-inmueble': FormularioCrearInmueble,
    'actuliza-inmueble': FormularioCrearInmueble,
    'filtrado-inmueble': Filtrado,
    'confirmar': Confirmar
  },
  computed: {
    isCargando(){ return this.$store.getters['inmueble/getCargando']; },
    seleccionados:{
      get(){ return this.$store.getters['inmueble/getInmueblesSeleccionados'] },
      set(nuevoValor){
        this.$store.commit('inmueble/setInmueblesSeleccionados', nuevoValor);
      },
    },
    verificarInmuebles(){ return this.$store.getters['inmueble/getVerificarInmuebles'] },
    inmueble(){ return this.$store.getters['inmueble/getInmueble'] },
    inmuebles_verificados(){ return this.$store.getters['inmueble/getTotalInmueblesVerificados'] },
    numeroInmueblesCreados(){ return this.$store.getters['inmueble/getNumeroInmueblesCreados'] },
    indice_inmueble:{
      get(){ return this.$store.getters['inmueble/getIndiceInmueble'] },
      set(nuevoValor){
        this.$store.commit('inmueble/setIndiceInmueble', nuevoValor)
      }
    }
  },
  methods: {
    async setInmueble(inmueble){
      this.indice_inmueble = this.inmuebles.indexOf(inmueble)
      if(Object.hasOwn(inmueble, 'id')){
        await this.recuperarInmueble(inmueble.id)
      } else {
        this.$store.commit('inmueble/setInmueble', {
          nombre: inmueble.nombre,
          clave: inmueble.clave,
          sirh: inmueble.sirh,
          cliente: inmueble.cliente,
          estado: inmueble.estado,
          municipio: inmueble.municipio,
          direccion: inmueble.direccion
        })
      }
      this.is_formulario_modeficar_inmueble=true
      await this.recuperarEstados()
      await this.recuperarMunicipiosDeEstado(this.inmueble.estado)
    },
    async setDialog({is_dialogo}){
      this.is_formulario_crear_inmueble=is_dialogo
      this.$store.commit('inmueble/cleanInmueble')
    },
    async setDialogModificarInmueble({is_dialogo, obj_inmueble_creado, obj_inmueble_modificado}){
      this.is_formulario_modeficar_inmueble=is_dialogo
      if(this.inmuebles_verificados.length !== 0 && this.verificarInmuebles.length !== 0){
        if(obj_inmueble_creado){ this.splitInmueble(this.indice_inmueble) }
        if(!obj_inmueble_creado && obj_inmueble_modificado) {
          await this.$store.dispatch('inmueble/setStatusInmuebleVerificado', {inmueble: this.inmueble, indice: this.indice_inmueble}) /** Cada vez se cerró el diálogo del formulario Crear Inmueble nuevo, verificará si está correcto. */
          this.$store.commit('inmueble/updateInmuebleVerificadoPorIndice', {inmueble: this.inmueble, indice: this.indice_inmueble})
        }
      }
      this.$store.commit('inmueble/cleanInmueble')
    },
    eliminarInmueble({confirmacion, indice, id}){
      if(confirmacion){
        if(0 < id){
          this.indice_inmueble = indice
          this.$store.dispatch('inmueble/eliminarInmueble', id)
        } else {
          this.$store.commit('inmueble/cleanInmueble')
          this.splitInmueble(indice)
        }
      }
    },
    async recuperarInmueble(id_inmueble){
      const { error } = await this.$store.dispatch('inmueble/recuperarInmueble', { id_inmueble: id_inmueble })
      if(error){
        this.$store.commit('alerta/addAlertas', {
          error: error
        })
      }
    },
    clearInmueblesVerificados(){
      this.$store.commit('inmueble/cleanInmueblesVerificados')
      this.$store.commit('inmueble/cleanVerificarInmuebles')
      this.$emit('emitBorrarInmueblesVerificados')
    },
    splitInmueble(indice){
      this.$store.commit('inmueble/splitInmueblesVerificados', indice)
      this.$store.commit('inmueble/splitVerificarInmuebles', indice)
    },
    async crearMultipleInmuebles(){
      const {error, estado} = await this.$store.dispatch('inmueble/crearMultiplesInmuebles', this.inmuebles)
      this.$store.commit('alerta/addAlertas',{ 
        estado: estado, mensaje: `Se crearon ${this.numeroInmueblesCreados} de ${this.inmuebles_verificados} inmuebles.`, error: error
      })
    },
    async recuperarEstados(){
      if(!this.$store.getters['ubicacion/getEstados'].length){
        await this.$store.dispatch('ubicacion/obtenerEstados');
      }
    },
    async recuperarMunicipiosDeEstado(obj_estado){
      if(obj_estado){
        await this.$store.dispatch('ubicacion/obtenerMunicipiosDeEstado', obj_estado.id);
      }
    },
    eventActualizar(){
      this.eventFiltro({ cliente: {}, estado: {}, municipio: {} })
    },
    irCrearMultiplesInmuebles({deshabilitar_autocomplete_cliente}){
      if (deshabilitar_autocomplete_cliente) { this.$store.commit('cliente/cleanCliente') }
      this.$router.push({ name: 'cargar-archivo', params: { is_cliente: deshabilitar_autocomplete_cliente} })
    },
    setPageText({pageStart, pageStop, itemsLength}) { // p - Tp de Fp {page, itemsPerPage, pageStart, pageStop, pageCount, itemsLength}
      this.pageText = `${pageStart + 1} - ${pageStop} de ${itemsLength}`
    },
    eventFiltro({ cliente, estado, municipio }){
      this.$emit('emitFiltro', { cliente, estado, municipio })
    }
  }
};
</script>
