<template>
    <v-dialog v-model="dialog" transition="dialog-top-transition" max-width="500" persistent>
        <template v-slot:default="dialog">
            <v-sheet class="container">
                <v-card class="mx-auto" :loading="cargando" outlined>
                    <v-card-title class="grey lighten-2">
                        Crear un nuevo proyecto
                        <v-spacer />
                        <v-btn @click="cerrrarDialogo(dialog, false)" color="error" icon><v-icon>mdi-close</v-icon></v-btn>
                    </v-card-title>
                    <v-card-text>
                        <br>
                        <v-form v-model="valido_form" ref="form_crear_nuevo_proyecto" lazy-validation>
                            <v-text-field v-model="nombre" label="Nombre del proyecto" filled outlined clearable dense :rules="proyecto_reglas" required/>
                        </v-form>
                    </v-card-text>
                    <v-card-actions>
                        <v-btn @click="crear(dialog)" color="green white--text" :disabled="!valido_form" block>Crear</v-btn>
                    </v-card-actions>
                </v-card>
            </v-sheet>
        </template>
    </v-dialog>
</template>

<script>
export default {
    props: {
        dialog: { type: Boolean, default: false }
    },
    data: () => ({
        nombre_proyecto: '',
        proyecto_reglas: [
            v => !!v || 'Escribe el nombre del proyecto.',
            v => (v && 3 <= v.length) || 'El nombre del proyecto debe tener 3 caracteres o más.'
        ],
        valido_form: true
    }),
    computed: {
        cargando(){ return this.$store.getters['proyecto/getCargando'] },
        nombre: {
            get(){ return this.nombre_proyecto },
            set(nuevo_valor){ this.nombre_proyecto = nuevo_valor == null ? '' : nuevo_valor }
        }
    },
    methods: {
        async crear(dialog) {
            if(this.$refs.form_crear_nuevo_proyecto.validate()){
                const { nombre } = this
                const { mensaje, estado, error } = await this.$store.dispatch('proyecto/crearProyecto', nombre );
                this.$store.commit('alerta/addAlertas', { estado: estado, mensaje: mensaje, error: error })
                if(!error){
                    this.cerrrarDialogo(dialog, true)
                }
            }
        },
        cerrrarDialogo(dialog, proyecto_creado){
            dialog.value = false
            this.$emit('emitDialog', { dialog: false, proyecto_creado: proyecto_creado })
            this.$refs.form_crear_nuevo_proyecto.reset()
        }
    }
};
</script>
