<template>
    <v-snackbar v-model="snackbar" right top color="transparent" >
        <v-alert
            v-for="(alerta, key) in alertas"
            :key="key"
            :type="tipoAlerta(alerta)"
            border="left"
            colored-border
        >
        <template v-slot:close>
            <v-btn icon right @click="cerrarAlerta(key)"><v-icon>mdi-close</v-icon></v-btn>
        </template>
        <div v-if="alerta.error">
            <span v-if="alerta.error.response.data.error">
                {{alerta.error.response.data.error}} <br>
            </span>
            <v-btn v-if="!ver_mas" @click="ver_mas=true" text  color="primary">Ver mas...</v-btn>
            <br>
            <span v-if="alerta.error.response.data.message && ver_mas">
                {{alerta.error.response.data.message}}<br>
                <!-- <b>{{ Object.hasOwn(alerta.error.response.data, "error") ? Object.values(alerta.error.response.data.error)[0][0] : '' }}</b> -->
            </span>
        </div>
        <div v-else>
            <span v-text="alerta.mensaje" />
        </div>
        </v-alert>
    </v-snackbar>
</template>

<script>
export default {
    data: () => ({
        snackbar: false,
        ver_mas: false
    }),
    computed: {
        alertas() { return this.$store.getters['alerta/getAlertas'] },
    },
    methods: {
        tipoAlerta({ estado, error }) {
            const status = error ? error.response.status : estado;
            switch (true) {
                case (status < 226):
                    return 'success';
                case (status < 451):
                    return 'warning';
                default:
                    return 'error';
            }
        },
        cerrarAlerta(indice){
            this.$store.commit('alerta/removeAlerta', indice)
            this.snackbar = this.alertas.length ? true : false 
        }
    },
    watch: {
        alertas(){
            this.snackbar = this.alertas.length ? true : false  
        },
        snackbar(nuevoValor){
            if(!nuevoValor){
                this.ver_mas = false
                this.$store.commit('alerta/clearAleras')
            }
        }
    }
};
</script>
